".mov\", \".3gp\", \".webm\"],\r\n  \"t-audio\": [\".wav\", \".mp3\", \".ogg\", \".acc\"],\r\n  \"t-word\": [\".docx\", \".doc\"],\r\n  \"t-excel\": [\".xlsx\", \".xls\"],\r\n  \"t-ppt\": [\".ppt\", \".pptx\"],\r\n  \"t-document\": [\".pdf\", \"t-word\", \"t-excel\", \"t-ppt\"],\r\n  \"t-zip\": [\".zip\", \".rar\"],\r\n};\r\n\r\nexport default {\r\n  props: {\r\n    modelValue: {\r\n      type: Array,\r\n      required: false,\r\n      default() {\r\n        return [];\r\n      },\r\n    },\r\n    triggerId: {\r\n      // \r\n      type: String,\r\n      required: false,\r\n      default: \"upload_image_trigger\" + parseInt(Math.random() * 1e8),\r\n    },\r\n    limitSize: {\r\n      // \r\n      type: Number,\r\n      required: false,\r\n      default() {\r\n        return 100 * 1024 * 1024;\r\n      },\r\n    },\r\n    imgCompress: {\r\n      // \r\n      type: Boolean,\r\n      required: false,\r\n      default() {\r\n        return true;\r\n      },\r\n    },\r\n    imgCompressOption: {\r\n      // \r\n      type: Object,\r\n      required: false,\r\n      default() {\r\n        return {\r\n          maxWidth: 1000,\r\n          maxHeight: 1000,\r\n        };\r\n      },\r\n    },\r\n    imgCrop: {\r\n      // \r\n      type: Boolean,\r\n      required: false,\r\n      default() {\r\n        return false;\r\n      },\r\n    },\r\n    imgCropOption: {\r\n      // \r\n      type: Object,\r\n      required: false,\r\n      default() {\r\n        return {\r\n          ratio: 1,\r\n          minWidth: 0,\r\n          minHeight: 0,\r\n          maxWidth: 1000,\r\n          maxHeight: 1000,\r\n        };\r\n      },\r\n    },\r\n    uploadMethod: {\r\n      // file/blob, fileName\r\n      type: Function,\r\n      required: false,\r\n    },\r\n    responseTransfer: {\r\n      //   fileList \r\n      type: Function,\r\n      required: false,\r\n      default(response) {\r\n        return response;\r\n      },\r\n    },\r\n  },\r\n  data() {\r\n    return {\r\n      dialo